.burger {
  --burger-width: 2.3rem;
  --burger-height: 1.8rem;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
@media (max-width: 768px) {
  .burger {
    display: block;
  }
